  background-image: url('data:image/svg+xml,<svg width="14" height="2" viewBox="0 0 14 2" fill="none" xmlns="http://www.w3.org/2000/svg"><rect opacity="0.2" width="14" height="2" fill="%23373737"/></svg>');
  background-repeat: no-repeat;
  background-position: center right;

  &:hover {
    background-image: url('data:image/svg+xml,<svg width="14" height="2" viewBox="0 0 14 2" fill="none" xmlns="http://www.w3.org/2000/svg"><rect opacity="0.2" width="14" height="2" fill="%23373737"/></svg>');
  }
