.pagetablr_indicator {
  display: block;
  background-position: center;
  background-repeat: no-repeat;
  width: 20px;
  height: 20px;
  margin-right: 5px;

  &:last-child {
    margin-right: 0;
  }
}

.lkpagetable__ttn {
  font-family: var(--font-family);
  font-weight: 700;
  font-size: 14px;
  line-height: 100%;
  color: #373737;
  display: block;
}

.lkpagetable__info {
  display: block;
  max-width: 220px;
  margin-top: 7px;
  text-overflow: ellipsis;
  overflow: hidden;
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 13px;
  line-height: 100%;
  color: #373737;
}

.lkpagetable_ttntable {
  .lkpagetable__table {
    tbody {
      td {
        padding: 25px 9px;
      }
    }
    thead {
      span {
        padding: 13px 9px;
      }
    }
  }
}

.green {
  color: #49b48d;
}

.lkpagetable__ttnstatus span,
.lkpagetable__ttnstatus span {
  text-align: right !important;
  width: min-content;
}

.lkpagetable__ttnstatvalue {
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 14px;
  line-height: 100%;
  color: #373737;
}

.lkpagetable__ttnstatvalue {
  background-position: left center;
  background-repeat: no-repeat;
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 14px;
  line-height: 100%;
  color: #373737;
  padding-left: 21px;
  display: inline-block;
  width: min-content;
  text-align: right;
}

.lkpagetable__ttnstatvalue_in {
  padding-left: 21px;
  background-image: url('data:image/svg+xml,<svg width="12" height="12" viewBox="0 0 12 12" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M6 3C4.3458 3 3 4.3458 3 6C3 7.6542 4.3458 9 6 9C7.6542 9 9 7.6542 9 6C9 4.3458 7.6542 3 6 3Z" fill="%2349B48D" /><path d="M6 1.5C3.51737 1.5 1.5 3.51737 1.5 6C1.5 8.48263 3.51737 10.5 6 10.5C8.48263 10.5 10.5 8.48263 10.5 6C10.5 3.51737 8.48263 1.5 6 1.5Z" stroke="%2349B48D" stroke-opacity="0.3" stroke-width="3" /></svg>');
}

.lkpagetable__ttnstatus {
  width: min-content;
  white-space: nowrap;
}
.lkpagetable_ttn {
  .lkpagetable__table thead th.lkpagetable__ttnstatus,
  .lkpagetable__table tbody td.lkpagetable__ttnstatus {
    text-align: right;
    padding-right: 35px;

    span {
      display: inline;
      text-align: right;
    }
  }
}

.lkpagetable_ttntable {
  .check {
    display: block;
    line-height: 1;
    text-align: right;
  }
  .check label {
    height: 18px;
    line-height: 1;
    padding-left: 18px;
  }
  .check label:before {
    top: 0;
    transform: none;
    border: 1px solid #d0d0d0;
    background: #fff;
  }
  .check input[type="checkbox"]:checked + label:before {
    background-color: $red;
    border-color: $red;
  }
}
.lkpagetable__ttn {
  white-space: nowrap;
}

.pageheader__back {
  a {
    margin-bottom: 27px;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 14px;
    line-height: 100%;
    text-align: center;
    color: rgba(55, 55, 55, 0.5);

    &:hover {
      color: rgb(55, 55, 55);
    }
  }

  span {
    padding-left: 7px;
    line-height: 1;
  }
}
.ordertoolbar__item {
  display: block;
  min-width: 22px;
  max-width: 22px;
  height: 22px;
}

.pageheader__main {
  h1 {
    margin-bottom: 0;
  }
}

.ordertoolbar__items {
  gap: 14px;
}

