.tracking-list {
  width: 270px;
}
.tracking-item {
  border-left: 2px solid #ff4d42;
  position: relative;
  padding: 0 0 23px 23px;
  font-size: 0.9rem;
  min-height: 39px;
}

.tracking-item .tracking-icon {
  position: absolute;
  left: -1px;
  transform: translateX(-50%);
  width: 16px;
  height: 16px;
  text-align: center;
  border-radius: 50%;
}

.tracking-item-pending,
.tracking-item-lastofactive {
  border-color: #e6e6e6;
}
.tracking-item:last-child {
  border-color: transparent;
  padding-bottom: 0;
  min-height: unset;
}

.tracking-item .tracking-icon {
  color: #ff4d42;
}
.tracking-item .tracking-icon circle:last-child {
  fill: #ff4d42;
}
.tracking-item .tracking-icon circle:first-child {
  stroke: #ff4d42;
}
.tracking-item.tracking-item-pending .tracking-icon circle:last-child {
  fill: #e6e6e6;
}
.tracking-item.tracking-item-pending .tracking-icon circle:first-child {
  stroke: #e6e6e6;
}

.tracking-content {
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;

  font-weight: 500;
  font-size: 14px;
  line-height: 100%;
  color: #373737;
}
.tracking-item-pending .tracking-content {
  opacity: 0.4;
}

