.dpswitch {
  position: relative;
  display: inline-block;
  width: 45px;
  height: 27px;
  margin-bottom: 0;
  display: block;
  line-height: 1;
}

/* Hide default HTML checkbox */
.dpswitch input {
  opacity: 0;
  width: 0;
  height: 0;
}

/* The slider */
.dpslider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ebebeb;
  border-radius: 20px;
  padding: 3px 3px;
  width: 38px;
  height: 22px;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

.dpslider:before {
  position: absolute;
  content: "";
  height: 16px;
  width: 16px;
  left: 3px;
  bottom: 3px;
  background-color: #fff;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

.dpswitch input:checked + .dpslider {
  background-color: #46d981;
  border-color: #46d981;
}

.dpswitch input:checked + .dpslider:before {
  -webkit-transform: translateX(16px);
  -ms-transform: translateX(16px);
  transform: translateX(16px);
  background: #fff;
}

/* Rounded sliders */
.dpslider.dpround {
  border-radius: 34px;
}

.dpslider.dpround:before {
  border-radius: 50%;
}

